.top{
  padding: 3%;
  margin: 2%;
  background-color:#343434;
  width: 40%;
  border-radius: 10px;
}
@media (max-width: 800px) {
  .top {
    width: 85%;
    padding: 3%;
    margin:2%;
  }
}
.upload-files{
  background-color:rgb(28, 28, 28);
  width: 90%;
  height:3rem;
  border-radius: 10px;
  margin-bottom: 1rem;
  padding: 3%;
  border-color: #c908bf;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size:1rem;
  color: white;
}
@media (max-width: 800px) {
  .upload-files {
    width: 85%;
    height: 2%;
    padding: 3%;
    margin:2%;
    font-size:0.8rem;
  }
}
.upload{
  background-color: #c908bf;
  height:3rem;
  width:40%;
  border-radius: 10px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size:1rem;
  color: white;
}
@media (max-width: 800px) {
  .upload {
    width: 45%;
    height: 2%;
    padding: 3%;
    margin:2%;
    font-size:0.8rem;
  }
}
#result{
  color: white;
  font-size:1rem;
}